import React, { useState, useEffect } from "react";
import axios from "axios";
import LoginSocMed from "../LoginSocMed/LoginSocMed.js";
import classes from "../../styles/LoginAndRegistration.module.css";
import Button from "../UI/Button/Button.js";
import Input from "../UI/Input/Input.js";
import useSendConfirmationEmail from "../../hooks/useSendConfirmationEmail";

function RegistrationConfirm({ user, setUser, isLogin, setEmailSent, setEmailSet, setModalVisible, setModalLogVisible, onEmailConfirmed }) {
    const [code, setCode] = useState('');
    const { sendEmail, loading, error, confirmCode } = useSendConfirmationEmail();

    useEffect(() => {
        const fetchData = async () => {
            await sendEmail(user.email);
        };
        fetchData();
        console.log("Start: " + confirmCode);

    }, []);

    const onSendEmailAgain = async () => {
        await sendEmail(user.email);
    }

    useEffect(() => {
        console.log("Effect: " + confirmCode);
    }, [confirmCode]);

    const handleCodeChange = (e) => {
        setCode(e.target.value);
    };

    const onConfirm = () => {
        setModalVisible(false);
        setModalLogVisible(true);
        setEmailSent(false);
        setUser({});
    }

    const onEmailChange = () => {
        setEmailSent(false);
        if (isLogin)
            setEmailSet(false);
    }

    const onSubmit = (e) => {
        e.preventDefault();

        if (code.includes(confirmCode)) {
            if (isLogin) {
                console.log("isLOgin: Succesful")
                onEmailConfirmed();
                return;
            }

            onConfirm();
            console.log(user);
            axios.post("https://localhost:7074/api/auth/register", user).then((result) => {
                console.log('Registration successful:', result.data);
                onConfirm();
            }).catch((err) => {
                console.error('Registration failed:', err);
            });;
        } else {
            alert("Code is incorrect try again!");
            setCode("");
        }
    };

    return (
        <div>
            <h2> </h2>
            <div className={classes.container}>
                <div className={classes.container}>
                    <form onSubmit={onSubmit}>
                        <div className={classes.container} style={{ flexDirection: 'column', gap: '0.5vw' }}>
                            <div>
                                <p className={classes.secondaryTxt}> {loading ? "Sending..." : "     "}<br />
                                    {user.email}
                                </p>
                                <div className={classes.container + ' ' + classes.text}>
                                    <p onClick={onEmailChange}> </p>
                                    <p onClick={onSendEmailAgain}>  </p>
                                </div>
                            </div>

                            <div>
                                <label> </label>
                                <Input
                                    type="text"
                                    id="code"
                                    name="code"
                                    placeholder=" "
                                    value={code}
                                    onChange={handleCodeChange}
                                    required
                                />
                            </div>
                        </div>

                        <div style={{ textAlign: 'end' }}>
                            <Button></Button>
                        </div>

                    </form>
                </div>

                <div className={classes.contBlock}></div>

                <LoginSocMed />
            </div>
        </div>
    );
};

export default RegistrationConfirm;
