import React from 'react';
import css from "./style.module.css"

function PostService({name, price, time, img}) {
    return (
        <div className={css.fAc}>
            <img alt='postLogo' src={img}/>
            <div>
                <h4>{name}</h4>
                <div> {price} .   {time} </div>
            </div>
        </div>
    );
}

export default PostService;